/* https://css-tricks.com/box-sizing/ */
html {
    box-sizing: border-box;
    -ms-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%;
    font: 100%/1.2em Arial,sans-serif;
    /* http://kyleschaeffer.com/development/css-font-size-em-vs-px-vs-pt-vs/ */
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
}

*, *:before, *:after {
    box-sizing: inherit;
}

/* http://jaydenseric.com/blog/forget-normalize-or-resets-lay-your-own-css-foundation */
html, body {
    margin: 0;
    padding: 0;
    min-width: 100%;
    width: 100%;
    max-width: 100%;
    min-height: 100%;
    height: 100%;
    max-height: 100%;
}

h1,
h2,
h3,
h4,
p,
blockquote,
figure,
ol,
ul {
    margin: 0;
    padding: 0;
    list-style: none;
}

main,
li {
    display: block;
}

h1,
h2,
h3,
h4 {
    font-size: inherit;
}

strong {
    font-weight: bold;
}

a,
button {
    color: inherit;
    -webkit-transition: .3s;
    transition: .3s;
}

button {
    overflow: visible;
    border: 0;
    font: inherit;
    -webkit-font-smoothing: inherit;
    letter-spacing: inherit;
    background: none;
    cursor: pointer;
}

::-moz-focus-inner {
    padding: 0;
    border: 0;
}

/*
:focus {
    outline: 0;
}
Be carefull with this! It removes the gross user agent default outline that appears when focusing interactive
elements. For accessibility only do this if you are absolutely sure you have custom focus state
styles set everywhere.
*/
img {
    max-width: 100%;
    height: auto;
    border: 0;
}

select {
    width: auto;
}